<template>
    <div>
        <h2>{{title}}</h2>
        <el-form>
            <div class="item-container" v-for="(item,index) in items" v-bind:key='item.id'>
                <el-form-item v-for="key in keys" v-bind:label="labels[key] || key" v-bind:key="key">
                    <el-input v-model="item[key]"></el-input>
                </el-form-item>
                <i class="el-icon-circle-close" v-on:click="removeWorkHistory(index)"></i>
                <hr>
            </div>
        </el-form>
    
        <el-button type="primary" v-on:click="addWorkHistory">添加</el-button>
    
    </div>
</template>

<script>
export default {
  props: ['items','labels','title'],
  computed:{
    keys(){
        return Object.keys(this.items[0])
    }
  },
  methods: {
        addWorkHistory() {
            const empty={}
            this.items.map((key)=>{
                empty[key] = ''
            })
            this.items.push(empty)
        },
        removeWorkHistory(index) {
            this.items.splice(index, 1) //从某个地方删除一个
        }
    }
}
</script>



